/*
  About
  ------------------------------------------------*/
.about {
    font-family: "Hashed Browns", "sans-serif";
    margin-left: 5%;
    padding-bottom: 5%;
    padding-top: 2.5%;
}

.chibi {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 60%
}

.box {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-left: 15%;
}

    .box > div {
        padding: 0px;
        margin: 0px;
    }
.about p {
    font-family: "Pumpkin Story", "sans-serif";
    padding: 10px;
}

@media screen and (max-width:749px) {
    #chibi {
      width: 90%;
    }
  }